<template>
  <v-ons-page>
    <v-ons-toolbar>
      <div class="left">
        <div class="toolbar-heading">検索</div>
      </div>
    </v-ons-toolbar>
    <v-ons-list>
      <v-ons-list-item>
        <v-ons-row>
          <v-ons-col class="label">
            性別
          </v-ons-col>
          <v-ons-col width="130px">
            <v-ons-select id="gender" v-model="gender">
              <option v-for="item in items" :value="item.value" :key="item.value">
                {{ item.text }}
              </option>
            </v-ons-select>
          </v-ons-col>
        </v-ons-row>
      </v-ons-list-item>
      <v-ons-list-item>
        <v-ons-row>
          <v-ons-col class="label">
            年齢
          </v-ons-col>
          <v-ons-col width="180px" class="right">
            <vue-slider v-model="ageValue" v-bind="options" ref="ageSelector"></vue-slider>
          </v-ons-col>

        </v-ons-row>
      </v-ons-list-item>
      <v-ons-list-item>
        <v-ons-row>
          <v-ons-col class="label">
            都道府県
          </v-ons-col>
          <v-ons-col width="130px">
            <v-ons-select id="prefecture" name="prefecture" v-model="selectedPrefecture">
              <option v-for="x in prefectures" :key="x.id" :value="x.id">{{x.name}}</option>
            </v-ons-select>
          </v-ons-col>
        </v-ons-row>
      </v-ons-list-item>
      <v-ons-list-item>
        <div class="right"><ons-button @click="search" class="ml10">検索</ons-button></div>
      </v-ons-list-item>
    </v-ons-list>
  </v-ons-page>
</template>

<script>
import SearchResultPage from './SearchResultPage'
import vueSlider from 'vue-slider-component'

export default {
  components: {
    vueSlider
  },
  data () {
    return {
      prefectures: [],
      selectedItem: -1,
      sliderRefreshed: false,
      items: [
        { text: '指定なし', value: -1 },
        { text: '男性', value: 0 },
        { text: '女性', value: 1 }
      ],
      gender: -1,
      selectedPrefecture: 0,
      ageValue: [
        20,
        29
      ],
      ageMin: 20,
      ageMax: 29,
      options: {
        width: '100%',
        height: 8,
        dotSize: 16,
        min: 20,
        max: 29,
        disabled: false,
        show: true,
        useKeyboard: true,
        tooltip: 'always',
        formatter: '{value}',
        enableCross: false,
        bgStyle: {
          'backgroundColor': '#fff',
          'boxShadow': 'inset 0.5px 0.5px 3px 1px rgba(0,0,0,.36)'
        },
        tooltipStyle: {
          'backgroundColor': '#666',
          'borderColor': '#666'
        },
        processStyle: {
          'backgroundColor': '#999'
        }
      }
    }
  },
  watch: {
    ageValue (val) {
      this.ageMin = val[0]
      this.ageMax = val[1]
    },
    '$root.activeTabIndex' (value) {
      if (value === 1 && !this.sliderRefreshed) {
        setTimeout(() => {
          this.refreshSlider()
        }, 500)
        this.sliderRefreshed = true
      }
    }
  },
  async mounted () {
    await this.getPrefectures()
  },
  activated () {
    console.log('activated')
  },
  deactivated () {
    console.log('deactivated')
  },
  methods: {
    refreshSlider () {
      this.$refs.ageSelector.refresh()
      console.log('refreshed')
    },
    async getPrefectures () {
      let response = await fetch('http://a1333.ml/api/prefecture', { mode: 'cors', credentials: 'include' })
      let json = await response.json()
      this.prefectures = json
    },
    async search () {
      console.log('age min - ', this.ageMin)
      console.log('age max - ', this.ageMax)
      let response = await fetch('http://a1333.ml/api/search', {
        method: 'post',
        headers: { 'Content-type': 'application/json; charset=UTF-8' },
        mode: 'cors',
        credentials: 'include',
        body: JSON.stringify(
          {
            gender: this.gender,
            age_min: this.ageMin,
            age_max: this.ageMax,
            prefecture: this.selectedPrefecture
          })
      })
      let json = await response.json()
      if (json.length > 0) {
        this.showSearchResultPage(json)
      } else {
        alert('検索結果: 0件')
      }
    },
    showSearchResultPage: function (users) {
      this.$root.activeTabIndex = this.$root.searchTabIndex
      while (this.$root.searchPageStack.length > 1) {
        this.$root.searchPageStack.pop()
      }
      this.$root.searchPageStack.push({
        extends: SearchResultPage,
        data () {
          return {
            users: users
          }
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
ons-list-title {
  text-transform: none;
}

ons-list-item {
  cursor: pointer;
}
.list-item {
  padding: 0;
}
.list-item__center {
  padding-left: 15px;
}
.label {
  padding-left: 15px;
}
</style>



